import React, { useState } from 'react';
import Ficha from './Ficha';

const Juego = (props) => {
    // Hooks
    const [juego, setJuego] = useState(props.juego);
    const [fichasJugadas, setFichasJugadas] = useState(
        {
            id1: null, letra1: null,
            id2: null, letra2: null,
            nFichasJugadas: 0,
        }
    );

    // Funciones útiles
    const manejarClick = (id) => {
        const juego_tr = [...juego];
        const fichasJugadas_tr = { ...fichasJugadas };
        if (!juego_tr[id].estado) {
            juego_tr[id].estado = true;

            if (fichasJugadas_tr.id1 === null) {
                // Se va a dar vuelta la primera ficha
                fichasJugadas_tr['id1'] = id;
                fichasJugadas_tr['letra1'] = juego_tr[id].letra;
                fichasJugadas_tr['nFichasJugadas'] = 1;
            }
            else {
                // Se va a dar vuelta la primera ficha
                fichasJugadas_tr['id2'] = id;
                fichasJugadas_tr['letra2'] = juego_tr[id].letra;
                fichasJugadas_tr['nFichasJugadas'] = 2;

                // chequeo de condición con 2 fichas
                if (fichasJugadas_tr.letra1.toLowerCase() === fichasJugadas_tr.letra2.toLowerCase()) {
                    // Match de letra
                    console.log('Le achuntaste... Letra: ' + fichasJugadas_tr.letra1.toLowerCase());
                    fichasJugadas_tr['id1'] = null;
                    fichasJugadas_tr['letra1'] = null;
                    fichasJugadas_tr['id2'] = null;
                    fichasJugadas_tr['letra2'] = null;
                    fichasJugadas_tr['nFichasJugadas'] = 0;
                }
                else{
                    // Sin match de letra, se devuelven las ficha giradas
                    juego_tr[id].estado = false;
                    juego_tr[fichasJugadas_tr['id1']].estado = false;

                    fichasJugadas_tr['id1'] = null;
                    fichasJugadas_tr['letra1'] = null;
                    fichasJugadas_tr['id2'] = null;
                    fichasJugadas_tr['letra2'] = null;
                    fichasJugadas_tr['nFichasJugadas'] = 0;
                }
            }

            console.log(fichasJugadas_tr.letra1 + ' - ' + fichasJugadas_tr.letra2);
            console.log('Click en la ficha: ' + id);
            setJuego(juego_tr);
            setFichasJugadas(fichasJugadas_tr);
        }
    }

    return (
        <div className='juego'>
            <div className='cabecera'>
                <h1>Ludimemoria</h1>
            </div>
            <div className='tablero'>
                {
                    juego.map(
                        (ltr) => (<Ficha letra={ltr.letra} key={ltr.id} estado={ltr.estado} handleClick={() => manejarClick(ltr.id)} />)
                        // En paréntesis redondo en el arrow func. Permite evitar hacer el return
                    )
                }
            </div>
            <div className='footer'>
                <h3>{fichasJugadas.letra1}</h3>
                <h3>{fichasJugadas.letra2}</h3>
            </div>
        </div>
    )
}

export default Juego;